import React, {
  useState,
  useEffect
} from 'react';
import styles from './form-user.module.css';
import {
  Button,
  Input,
  EmailInput,
  PasswordInput,
} from '@ya.praktikum/react-developer-burger-ui-components';
import {
  getUser,
  updateUser
} from '../../services/actions/user/user';
import {
  useDispatch,
  useSelector,
} from '../../utils/hooks';

export const FormUser = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getUser());
  }, [dispatch]);

  const { name, email } = useSelector((store) => store.user.user);

  const [state, setState] = useState({
    name: '',
    email: '',
    password: '',
  });

  //   
  const onChangeInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  // 
  const onSubmitForm = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    const newDataUser = {
      name: state.name !== name ? state.name : name,
      email: state.email !== email ? state.email : email,
      password: state.password,
    };

    dispatch(updateUser(newDataUser));
  };

  //   
  const onCancel = (e: React.SyntheticEvent) => {
    e.preventDefault();
    setState({
      name: name,
      email: email,
      password: '',
    });
  };

  const hasName = state.name !== name; // 
  const hasEmail = state.email !== email; // 
  const hasState = !!state.email && !!state.name; //    

  return (
    <form className={`${styles.form}`} onSubmit={onSubmitForm}>
      <Input
        type='text'
        placeholder={''}
        name='name'
        error={false}
        errorText=''
        size='default'
        onChange={onChangeInput}
        value={name ? name : state.name}
      />
      <EmailInput
        value={email ? email : state.email}
        name="email"
        onChange={onChangeInput}
      />
      <PasswordInput
        value={state.password}
        name="password"
        onChange={onChangeInput}
      />

      {(hasName || hasState) && (hasEmail || hasState) && (!!state.password && hasState)
        ? (<div className={styles.buttons} onClick={onCancel} >
          <span className={styles.button}>
            
          </span>
          <Button type='primary' size='medium'>
            
          </Button>
        </div>) : null}
    </form>
  )
};
