import React, { useState } from 'react';
import styles from "./forgot-password.module.css";
import {
  Link,
  Redirect
} from 'react-router-dom';
import {
  Button,
  EmailInput,
} from '@ya.praktikum/react-developer-burger-ui-components';
import { forgotPassword } from '../../services/actions/password/password';
import {
  useDispatch,
  useSelector,
} from '../../utils/hooks';

export const ForgotPassword = () => {
  const dispatch = useDispatch();

  const { isAuth } = useSelector((store) => store.user);

  const hasForgotPasswordRequest = useSelector((store) => store.password.forgotPasswordRequest);

  const [email, setEmailValue] = useState("");

  //   
  const onChangeEmail = (e: React.ChangeEvent<HTMLInputElement>) => {
    setEmailValue(e.target.value);
  };

  // 
  const onSubmitForm = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(forgotPassword(email));
  };

  if (isAuth) {
    return (
      <Redirect
        to={{
          pathname: '/'
        }}
      />
    );
  };

  if (hasForgotPasswordRequest) {
    return (
      <Redirect
        to={{
          pathname: 'reset-password'
        }}
      />
    );
  };

  return (
    <div className={styles.container}>
      <form onSubmit={onSubmitForm} className={`${styles.form} mb-20`}>
        <h1 className="text text_type_main-medium">
           
        </h1>
        <EmailInput
          onChange={onChangeEmail}
          value={email}
          name="email"
        />
        <Button
          type='primary'
          size='medium'
        >
          
        </Button>
      </form>
      <div className={styles.link_container}>
        <p className="text text_type_main-default text_color_inactive mr-2">
           ?
        </p>
        <Link
          to="/login"
          className={`${styles.link} text text_type_main-default`}
        >
          
        </Link>
      </div>
    </div>
  )
};
