import { AppHeader } from '../app-header/app-header';
import { Main } from '../../pages/main/main';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  useLocation,
} from 'react-router-dom';
import { Login } from '../../pages/login/login';
import { Register } from '../../pages/register/register';
import { ResetPassword } from '../../pages/reset-password/reset-password';
import { ForgotPassword } from '../../pages/forgot-password/forgot-password';
import { Profile } from '../../pages/profile/profile';
import { ProtectedRoute } from '../protected-route/protected-route';
import { IngredientDetails } from '../ingredient-details/ingredient-details';
import { Modal } from '../modal/modal';
import { OrderDetails } from '../order-details/order-details';
import { useEffect } from 'react';
import { getUser } from '../../services/actions/user/user';
import { Feed } from '../../pages/feed/feed';
import { Order } from '../../pages/order/order';
import { getIngredients } from '../../services/actions/ingredients/ingredients';
import { useDispatch } from '../../utils/hooks';

const ModalSwitch: React.FC = () => {
  const location = useLocation<{ background: any }>();
  const background = location.state && location.state.background;
  const hasToken = localStorage.getItem('refreshToken');
  const dispatch = useDispatch();
  
  useEffect(() => {
    if (hasToken) {
      dispatch(getUser());
    }
  }, [hasToken, dispatch]);

  return (
    <>
      <Switch location={background || location}>
        <Route path='/' exact>
          <Main />
        </Route>
        <Route path='/login' exact>
          {' '}
          {/* */}
          <Login />
        </Route>
        <Route path='/register' exact>
          {' '}
          {/* */}
          <Register />
        </Route>
        <Route path='/forgot-password' exact>
          {' '}
          {/*  */}
          <ForgotPassword />
        </Route>
        <Route path='/reset-password' exact>
          {' '}
          {/*  */}
          <ResetPassword />
        </Route>
        <Route path='/feed' exact>
          {' '}
          {/*  .   .*/}
          <Feed />
        </Route>
        <Route path='/feed/:number' exact>
          {' '}
          {/*   .   .*/}
          <Order />
        </Route>
        <ProtectedRoute path='/profile/orders/:number' exact children={<Order />} />
        <ProtectedRoute path='/profile'>
          {' '}
          {/*   */}
          <Profile />
        </ProtectedRoute>
        <Route path='/ingredients/:id' exact>
          {' '}
          {/* */}
          <IngredientDetails />
        </Route>
      </Switch>
      {background && (
        <>
          <Route path='/' exact>
            <Modal>
              <OrderDetails />
            </Modal>
          </Route>
          <Route path='/ingredients/:id'>
            <Modal>
              <IngredientDetails />
            </Modal>
          </Route>
          <Route path='/feed/:number'>
            <Modal>
              <Order />
            </Modal>
          </Route>
          <ProtectedRoute path='/profile/orders/:number'>
            <Modal>
              <Order />
            </Modal>
          </ProtectedRoute>
        </>
      )}
    </>
  );
};

export const App = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getIngredients());
  }, [dispatch]);

  return (
    <Router basename='/react-burger'>
      <AppHeader />
      <ModalSwitch />
    </Router>
  );
};
