import React, { useState } from "react";
import styles from "./register.module.css";
import {
  Link,
  Redirect,
} from 'react-router-dom';
import {
  Button,
  Input,
  PasswordInput,
  EmailInput,
} from '@ya.praktikum/react-developer-burger-ui-components';
import { register } from "../../services/actions/user/user";
import {
  useDispatch,
  useSelector,
} from "../../utils/hooks";

export const Register = () => {
  const dispatch = useDispatch();

  const { isAuth } = useSelector((store) => store.user);

  const [state, setState] = useState({
    name: '',
    email: '',
    password: '',
  });

  //   
  const onChangeInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  // 
  const onSubmitForm = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(register(state));
  };

  if (isAuth) {
    return (
      <Redirect
        to={{
          pathname: '/'
        }}
      />
    );
  };

  return (
    <div className={styles.container}>
      <form onSubmit={onSubmitForm} className={`${styles.form} mb-20`}>
        <h1 className="text text_type_main-medium"></h1>
        <Input
          type={'text'}
          placeholder={''}
          value={state.name}
          name={'name'}
          error={false}
          errorText={''}
          size={'default'}
          onChange={onChangeInput}
          icon='EditIcon'
        />
        <EmailInput
          value={state.email}
          name="email"
          onChange={onChangeInput}
        />
        <PasswordInput
          value={state.password}
          name={'password'}
          onChange={onChangeInput}
        />
        <Button type='primary' size='medium'>
          
        </Button>
      </form>
      <div className={styles.link_container}>
        <p className="text text_type_main-default text_color_inactive mr-2"> ?</p>
        <Link
          to='/login'
          className={`${styles.link} text text_type_main-default`}
        >
          
        </Link>
      </div>
    </div>
  )
};
